/**
 * 转化坐标
 * @param {Object} origin
 * @param {Array} center
 */
export function convertCoordinateOrigin(origin, center) {
  let [centerX, centerY] = center

  return {
    x: centerX + origin.x,
    y: centerY - origin.y,
  }
}

/**
 * 判断是否有冲突
 * @param {Object} souce // 待检查对象
 * @param {Object} target // 比较对象
 * @param {Object} distance // 避免冲突的间隙
 */
export function isCollision(souce, target, distance) {
  let isCollision = false
  if (souce.x > 0 && target.x > 0) {
    isCollision = souce.y + distance > target.y
  } else if (souce.x < 0 && target.x < 0) {
    isCollision = souce.y - distance < target.y
  }

  return isCollision
}

/**
 * 检查并避免冲突
 * @param {Object} souce // 待检查对象
 * @param {Object} target // 比较对象
 * @param {Object} distance // 避免冲突的间隙
 */
export function avoidCollision(souce, target, distance) {
  if (target) {
    while (isCollision(souce, target, distance)) {
      if (souce.x > 0) {
        souce.y--
      } else if (souce.x < 0) {
        souce.y++
      }
    }
  }
  return souce
}

/**
 * 10% => 0.1
 * @param {String} percent
 */
export function percentToNum(percent) {
  if (!!percent) {
    percent = String(percent).replace('%', '')
    percent = Number(percent) / 100
  } else {
    percent = 0
  }
  return percent
}

/**
 * 判断一个数据是否为对象
 * @param {Any} data
 */
export function isObject(data) {
  return Object.prototype.toString.call(data) === '[object Object]'
}

/**
 * 判断一个数据是否为数组
 * @param {Any} data
 */
export function isArray(data) {
  return Object.prototype.toString.call(data) === '[object Array]'
}
